.innerDiv{
    position: absolute;
    left: 21%;
    margin-top: 20px;
    font-size: 30px;
    font-weight: 600;
}
.filter{
    font-size: 17px;
    margin-top: 40px;
}
.filter img{
    margin-top: 5px;
    width: 15px;
}
.btns button{
    display: inline-block;
    margin-right: 10px;
    height: 30px;
    border-radius: 17px;
    border: 1px solid #0064FF;
    color: #0064FF;
}
button:first-child{
    width: 50px;
    /* background-color: #0064FF; */
    color: #0064FF;
}
button:hover{
    transform: scale(1.06, 1.02);
    cursor: pointer;
}
.active{
    background-color: #0064FF;
    color: aliceblue;
}
.btns{
    width: 800px;
}
