.card{
    width: 250px;
    margin-right: 20px;
    margin-bottom: 20px;
    border: 1px solid #0074B7;
    height: 370px;
    border-radius: 10px;
    box-shadow: 0 5px 6px -6px black;
}
.card:hover{
    cursor: pointer;
    /* box-shadow: 3px 8px 6px -6px black; */
    box-shadow: 0 0 8px -2px #444444;
}
.card div img{
    width: 100%;
    height: 140px;
    border-radius: 10px;
}
.card a, .card p{
    display: inline;
    padding-left: 10px;
    display: inline-block;
}
.card a{
    text-decoration: none;
    color: #0074B7;
}
.heading1{
    line-height: 2.5ex;
    height: 5ex;
    width:240px;
    overflow:hidden;
    text-overflow: ellipsis;
    padding-right: 40px;
    font-size: 20px;
    font-weight: 600;
}
.heading1{
    padding-left: 10px;
    padding-right: 20px;  
}
.content{
    line-height: 2.5ex;
    height: 10.5ex;
    width: 240px;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 5px 40px 20px 10px;
    color: grey;
}
.card-container div{
    display: inline-block;
}
.card-container{
    position: absolute;
    top: 53%;
    left: 21%;
    width: 900px;
}