body{
    background-color: aliceblue;
}
.nav{
    display: flex;
    align-items: center;
    /* padding-top: 20px; */
    padding-left: 40px;
    background-color: white;
    box-shadow: 0 4px 6px -6px black;
}
.nav img{
    margin-right: 30px;
    width: 130px;
}

.nav p{
    position: relative;
    left: 30%;
    color: gray;
    width: 280px;
}
.nav button{
    position: absolute;
    right: 9%;
    height: 40px;
    width: 160px;
    border-radius: 20px;
    background-color: #0064FF;
    color: white;
    border: none;
}
.nav a{
    text-decoration: none;
    color: rgb(26, 25, 25);
    margin-left: 90px;
}
.nav div p{
    font-size: 30px;
    position: absolute;
    left: 40px;
    margin-top: 10px;
    font-weight: 800;
    color: #0064FF;
    display: inline-block;
}
.nav div h4{
    padding-top: 12px;
    font-size: 20px;
    font-weight: 500;
    color: gray;
}
