
.wrapper{
    display: flex;
    position: absolute;
    top: 180%;
    padding: 90px 30px 100px 30px;
    background-color: #132F78;
    margin-top: 20px;
}
.wrapper div a{
    display: grid;
}
.wrapper div{
    width: 300px;
}
.address{
    width: 300px;
}
.one{
    margin-left: 90px;
}
.wrapper div a{
    text-decoration: none;
    color: white;
    padding: 5px 0 5px 0;
}
.wrapper div h3{
    color: rgba(150, 150, 165, 0.725);
}
.wrapper div p{
    color: white;
}
